#navbar {
  display: grid;
  min-height: 100px;
  grid-auto-flow: column;
  background-color: #fcfcfc;
  grid-gap: 1.5em;
  // padding: 0 40px;
  // box-shadow: -1px 4px 5px 0px rgba(209, 205, 209, 1);
}

#logo {
  display: grid;
  justify-content: start;
  align-content: center;
  font-family: "Open Sans", sans-serif;
  font-size: 30px;
  font-weight: 700;
  color: #000;
  text-transform: uppercase;
}

.logo span {
  color: #0474bc;
}

#links {
  display: grid;
  justify-content: right;
  align-content: center;
  grid-auto-flow: column;
  grid-gap: 3.5em;
  font-family: "Open Sans", sans-serif;
}

#links a {
  text-decoration: none;
  color: #333;
  text-transform: uppercase;
  // font-weight: bold;
}

#links a:hover {
  color: #2f9839;
}

/* =======================
******* Mobile Menu ******
========================*/
.sidenav {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  background-color: #2f9839;
  overflow-x: hidden;
  transition: 0.5s;
  padding-top: 60px;
}

.sidenav a {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  // font-size: 25px;
  color: #fff;
  display: block;
  transition: 0.3s;
  white-space: nowrap;
}

.sidenav a:hover {
  color: #818181;
}

.sidenav .closebtn {
  position: absolute;
  top: 0;
  right: 25px;
  font-size: 36px;
  margin-left: 50px;
}

a {
  font-family: "Open Sans", sans-serif;
}

/* Mobile to Tablet  */
@media (min-width: 320px) and (max-width: 1199px) {
  #links,
  .mobile-menu {
    display: none;
  }
  #logo {
    display: inline-grid;
    grid-template-columns: auto auto;
    grid-gap: 70px;
  }
}

/* Tablet to Desktop */
@media (min-width: 1200px) {
  .mobile-btn {
    display: none;
  }
}


/* bread_crumb */
#bread_crumb {
  width: 100%;
  ul {
    padding: 31px 0;
    li {
      .home {
        padding: 0 0 0 5px;
      }
      display: inline;
      font-size: 12px;
      padding: 0 0 0 17px;
      margin: 0 5px 0 0;
      line-height: 1.6;
      position: relativ
      a {
        position: relative;
        width: 14px;
        height: 18px;
        word-wrap: break-word;
        text-indent: -300%;
        overflow: hidden;
        display: inline-block;
        box-sizing: border-box;
      }
    }
  }
}



/* ----------------------------------------------------------------------
 アーカイブページ
