/* ページング */
.p-page-navi { 
  margin:50px 0 0 0; 
  ul { 
    margin:0;
    font-size:0; 
    li { 
      display:inline;
      margin:0 5px 5px 0;
    }
  }
}
.p-page-navi a, .p-page-navi a:hover, .p-page-navi span { font-size:12px; padding:14px 16px; line-height:1; margin:0; background:#f7f7f7; border:1px solid #ddd; color:#000; display:inline-block; }
.p-page-navi a:hover { color:#fff; background:#000; border-color:#000; }
.p-page-navi span.dots { background:none; }
.p-page-navi span.current { color:#fff; background:#000; border-color:#000; }

.p-page-navi .next, .p-page-navi .prev { padding:14px 14px !important; }
.p-page-navi .next span, .p-page-navi .prev span { display:none; }
.p-page-navi .next:before, .p-page-navi .prev:before {
  font-family: 'Material Icons'; color:#333; font-size:11px;
  -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale;
}
.p-page-navi .prev:before { content:'keyboard_arrow_left'; }
.p-page-navi .next:before { content:'keyboard_arrow_right'; }
.p-page-navi .prev:hover:before, .p-page-navi .next:hover:before { color:#fff; }

#footer_social_link li {
  margin: 0;
  position: relative;
  display: inline-block;
}

